<template>
  <v-card class="mx-auto" outlined>
    <v-card-text>
        {{peticiones_verificadas}} <br>
        {{solicitud_recurso}}
    </v-card-text>
    <v-btn block color="success" @click="crearPeticiones()">Crear</v-btn>
  </v-card>
</template>

<script>
export default {
  computed:{
    peticiones_verificadas() { return this.$store.getters["MOD_pagos/getPeticionesVerificadas"] },
    solicitud_recurso(){ return this.$store.getters["MOD_pagos/getSolicitudRecurso"] }
  },
  methods:{
    async crearPeticiones(){
      const { mensaje, estado, peticiones, error } = await this.$store.dispatch("MOD_pagos/confirmarPeticiones", this.peticiones_verificadas)
      if(error){
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      } else {
        this.crearSolicitudRecurso(peticiones)
      }
    },
    async crearSolicitudRecurso(peticiones_confirmadas){
      const trabajos = []
      for(const peticion of peticiones_confirmadas){
        trabajos.push(peticion.id)
      }
      this.solicitud_recurso.trabajos = trabajos
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/crearRecurso", this.solicitud_recurso)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    }
  }
}
</script>