<template>
  <v-form v-model="valid" ref="formulario" lazy-validation>
    <v-row dense>
      <v-col cols="12">
        <v-combobox v-model="solicitud_recurso.tipo_pago"
          :items="pagos"
          label="Tipo de pago"
          :rules="[(v) => !!v || 'Es necesario elegir el tipo de pago']"
          chips
        />
      </v-col>

      <v-col cols="6">
        <v-combobox v-model="beneficiario"
          :items="beneficiarios"
          label="Nombre del beneficiario"
          item-text="nombre"
          :rules="[(v) => !!v || 'Es necesario elegir un beneficiario']"
          chips
          clearable
          return-object
          @change="beneficiario ? setBeneficiario(beneficiario) : setBeneficiario({'nombre': '', 'clave': null, 'numero_tarjeta': null})"
        />
      </v-col>
      <v-col cols="6">
        <formulario-crear-beneficiario />
      </v-col>

      <v-col cols="6">
        <v-text-field
          v-model="solicitud_recurso.clave"
          label="Clave"
          :rules="[(v) => !!v || 'Es necesario escribir la clave']"
        />
      </v-col>
      <v-col cols="6">
        <v-text-field v-model="solicitud_recurso.numero_tarjeta"
          label="Numero de tarjeta"
          :rules="[(v) => !!v || 'Es necesario escribir el numero de la tarjeta']"
        />
      </v-col>
      <v-col cols="6">
        <v-text-field v-model="solicitud_recurso.banco"
          label="Banco" :rules="[(v) => !!v || 'Es necesario escribir el banco']"
        />
      </v-col>
      <v-col cols="6">
        <v-text-field type="number" label="Importe" :value=total disabled />
      </v-col>
      <v-col cols="12">
        <v-textarea v-model="solicitud_recurso.concepto_pago" dense outlined
          label="Concepto de pago"
          :rules="[(v) => !!v || 'Es necesario escribir los datos bancarios']"
        />
      </v-col>
      <v-spacer />
    </v-row>
      <v-row
        no-gutters
        dense
      >
        <v-col>
          <v-simple-table dense>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Trabajo
                  </th>
                  <th class="text-left">
                    Inmueble
                  </th>
                  <th class="text-left">
                    Importe
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(peticion, index) in peticiones_verificadas" :key="peticion.trabajo">
                  <td>{{ trabajos_seleccionados[index].concepto }}</td>
                  <td>{{ trabajos_seleccionados[index].inmueble.name }}</td>
                  <td>$ {{peticion.monto}}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-col>
      </v-row>
  </v-form>
</template>

<script>
import crearNuevoBeneficiario from '../../components/Beneficiario/CrearBeneficiario.vue'
export default {
  data:() =>({
    pagos: ["Transferencia", "Cheque"],
    continuar_p4: true,
    solicitud_recurso: {
      trabajos: [],
      tipo_pago: '',
      beneficiario: '',
      banco: '',
      clave: '',
      numero_tarjeta: '',
      concepto_pago: ''
    },
  }),
  components:{
    'formulario-crear-beneficiario': crearNuevoBeneficiario
  },
  computed: {
    peticiones_verificadas() { return this.$store.getters["MOD_pagos/getPeticionesVerificadas"] },
    trabajos_seleccionados() { return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"]; },
    beneficiarios() { return this.$store.getters["beneficiario/getBeneficiarios"] },
    valid: {
      get(){ return this.continuar_p4 },
      set(nuevoValor){
        this.continuar_p4 = nuevoValor
        this.$emit("eventoContinuar", nuevoValor)
        if (this.valid) { this.$store.commit("MOD_pagos/setSolicitudRecurso", this.solicitud_recurso) }
      }
    },
    beneficiario: { 
      get(){ return this.$store.getters["beneficiario/getBeneficiario"] },
      set(nuevoValor){
        if(nuevoValor){ this.$store.commit("beneficiario/setBeneficiario", nuevoValor) }
        else { this.$store.commit("beneficiario/clearBeneficiario") }
      }
    },
    total() {
      var total = 0.0
      for(const peticion of this.peticiones_verificadas){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return total.toFixed(2)
    },
  },
  created(){
    this.inicializarBeneficiarios()
  },
  methods: {
    setBeneficiario({nombre, clave, numero_tarjeta}){
      this.solicitud_recurso.beneficiario = nombre
      this.solicitud_recurso.clave = clave
      this.solicitud_recurso.numero_tarjeta = numero_tarjeta
    },
    async inicializarBeneficiarios(){
      const { mensaje, estado, error } = this.$store.dispatch("beneficiario/obtenerBeneficiarios")
      if(error){
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      }
    }
  },
};
</script>