<template>
  <v-list dense>
    <v-list-item-group rounded>
      <v-list-item
        v-for="(cliente, i) in clientes"
        :key="i"
        link
        @click="irInmuebles(cliente)"
      >
        <v-list-item-avatar>
          <v-avatar color="grey" size="56" class="white--text">
            {{ cliente.nombre[0] }}
          </v-avatar>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title v-text="cliente.nombre" />
        </v-list-item-content>
        <!-- <v-hover v-slot="{ hover }">
          <v-list-item-icon>
            <v-btn :disabled="!hover" x-small icon>
              <v-icon v-text="'mdi-pencil'" />
            </v-btn>
            <v-btn :disabled="!hover" x-small icon @click="eliminar(cliente)">
              <v-icon v-text="'mdi-delete'" />
            </v-btn>
          </v-list-item-icon>
        </v-hover> -->
      </v-list-item>
      <span v-if="isCargando" class="container">Cargando...</span>
      <span v-if="!isCargando && !clientes.length" class="container">No hay clientes</span>
    </v-list-item-group>
  </v-list>
</template>

<script>
export default {
  computed: {
    clientes() { return this.$store.getters["MOD_clientes/getClientes"] },
    isCargando() { return this.$store.getters["MOD_clientes/getCargando"] }
  },
  created() {
    this.inicializarClientes();
  },
  methods: {
    eliminar(cliente) {
      const mensajeEliminar =
        "¿Estás seguro de que quieres eliminar al cliente " +
        cliente.nombre +
        "?";
      let confirmacion = confirm(mensajeEliminar);
      if (confirmacion) {
        const index = this.clientes.indexOf(cliente);
        const { mensaje, error } = this.$store.dispatch(
          "MOD_clientes/eliminar",
          { id: cliente.id, indice: index }
        );
        if (error) {
          alert(mensaje);
          return;
        }
        alert(mensaje);
      }
    },
    async inicializarClientes() {
      const {error, mensaje, estado } = await this.$store.dispatch("MOD_clientes/obtenerClientes");
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, 'error': error })
    },
    irInmuebles(cliente) {
      this.$store.dispatch("MOD_clientes/recuperarCliente", cliente);
      this.$router.push({
        name: "inmuebles",
        params: { cliente: cliente.nombre },
      });
    },
  },
};
</script>
