<template>
  <v-card class="mx-auto" :loading="isCargando">
    <nuevo-cliente />
    <listar-clientes />
  </v-card>
</template>

<script>
import CrearCliente from "../../components/Cliente/FormularioCrearCliente.vue";
import ListarClientes from "../../components/Cliente/MostrarClientes.vue"
export default {
  components: {
    "nuevo-cliente": CrearCliente,
    "listar-clientes": ListarClientes
  },
  computed: {
    isCargando() {
      return this.$store.getters["MOD_clientes/getCargando"];
    },
  },
};
</script>
