<template>
  <v-form ref="formulario" v-model="valid" lazy-validation>
    <v-row no-gutters>
      <v-col cols="12">
        <v-toolbar flat><v-toolbar-title>Fondos necesarios: </v-toolbar-title><v-spacer /><v-toolbar-title>MX$ {{total}}</v-toolbar-title></v-toolbar>
      </v-col>
    </v-row>
    <v-divider class="black"/>
    <v-row
      no-gutters
      dense
      v-for="(peticion, index) in peticiones_verificadas"
      :key="peticion.trabajo"
    >
      <v-col cols="5">
        <b>{{ trabajos_seleccionados[index].concepto }}</b>: {{ trabajos_seleccionados[index].inmueble.name }}
      </v-col>
      <v-col cols="5">
        <v-text-field
          v-if="!peticion.error"
          v-model="peticion.monto"
          label="Importe"
          solo
          dense
          autofocus
          prefix="$"
          type="number"
          placeholder="1.00"
          step="1"
          min="1"
          :rules="[(v) => v>0 || 'Es necesario escribir el monto mayor a 0.00' ]"
        />
        <v-chip v-else class="error" outlined>{{peticion.error}}</v-chip>
      </v-col>
      <v-spacer></v-spacer>
      <v-col cols="1">
        <v-btn icon>
          <v-icon>mdi-trash-can</v-icon>
        </v-btn>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
export default {
  data: () => ({
    continuar_p3: true,
  }),
  computed: {
    trabajos_seleccionados() { return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"]; },
    peticiones_verificadas() { return this.$store.getters["MOD_pagos/getPeticionesVerificadas"] },
    total() {
      var total = 0.0
      for(const peticion of this.peticiones_verificadas){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return total.toFixed(2)
    },
    valid: {
      get(){ return this.continuar_p3 },
      set(nuevoValor){
        this.continuar_p3 = nuevoValor
        this.$emit("eventoContinuar", nuevoValor)
      }
    }
  },
  methods: {
    cambiarMonto(peticion, index){
      this.$store.commit("MOD_pagos/setMontoPeticionesVerificadas", {'editar_peticion': peticion, 'editar_index': index})
    },
  }
};
</script>
