<template>
  <v-dialog
    v-model="dialogRecurso"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <v-card class="mx-auto ma-5">
        <v-toolbar dark color="primary" flat>
          <v-btn x-large icon dark @click="dialogRecurso = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-row>
            <v-col cols="12" sm="8">
              <v-row>
                <v-col cols="12" sm="12">
                  <v-data-table
                    :headers="headers"
                    :items="recurso.peticiones"
                    loading-text="Cargando... por favor espere"
                    :footer-props="{
                      itemsPerPageText: 'Filas por página',
                      pageText: '',
                    }"
                    no-data-text="No tienes trabajos."
                    class="elevation-5 row-pointer"
                    :loading="cargando"
                  ></v-data-table>
                </v-col>
                <v-col cols="12" sm="12">
                  <v-row>
                    <v-col cols="12" sm="6">
                      <v-text-field v-model="nombre" dense solo label="Nombre del archivo" />
                    </v-col>
                    <v-col cols="12" sm="6">
                      <v-file-input
                        solo
                        dense
                        v-model="PDFBase64"
                        accept=".pdf, .xmls"
                        show-size
                        truncate-length="6"
                        :append-outer-icon="PDFBase64 ? 'mdi-cloud-upload' : 'mdi-send-off'"
                        @click:append-outer="subirArchivo"
                      ></v-file-input>
                    </v-col>
                    <v-col cols="12" sm="12">
                      <v-card class="mx-auto">
                        <v-list>
                          <v-subheader>{{archivos.length ? 'Comprobantes' : 'No se han subido comprobantes.'}}</v-subheader>
                            <v-list-item v-for="(archivo, index) in archivos" :key="index">
                              <v-list-item-content>
                                <a :href="archivo.archivo">{{archivo.nombre}}</a>
                              </v-list-item-content>
                              <v-list-item-action>
                                <confirmar
                                  mensaje="Estás a punto de eliminar esta archivo. ¿Estás seguro?"
                                  :id="archivo.id"
                                  :indice="index"
                                  :tipo_pago="('¿Eliminar ' + archivo.nombre +'?')"
                                  @emitConfirmacion="eliminarArchivoRecurso"
                                  icon="mdi-delete"
                                  color="error"
                                />
                              </v-list-item-action>
                            </v-list-item>
                        </v-list>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="12" sm="4">
              <Comentarios />
            </v-col>
          </v-row>
        </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import Confirmar from "../../alertas/Confirmar.vue";
import Comentarios from "../../components/Recurso/Comentarios.vue";

export default {
  props: {
    id_recurso: { type: Number, default: 0 },
    dialog: { type:Boolean, default:false},
    nombre_archivo: { type: String, default: ''}
  },
  data: () => ({
    headers: [
      { text: "Solicitado", value: "trabajo.solicitado" },
      { text: "Gestor", value: "trabajo.gestor" },
      { text: "Cliente", value: "inmueble.cliente" },
      { text: "Clave", value: "inmueble.clave" },
      { text: "Sucursal", value: "inmueble.nombre", sortable: false },
      { text: "Estado", value: "inmueble.estado", sortable: false },
      { text: "Municipio", value: "inmueble.municipio", sortable: false },
      { text: "Concepto", value: "trabajo.concepto", sortable: false },
      { text: "Importe", value: "importe", sortable: false },
    ],
    PDFBase64: null,
    nombre: 'Comprobante de validación'
  }),
  components: {
    Comentarios,
    Confirmar
  },
  computed: {
    recurso() { return this.$store.getters["MOD_pagos/getRecurso"]; },
    archivos() { return this.$store.getters["MOD_pagos/getArchivos"] },
    dialogRecurso:{
      get() { return this.dialog },
      set(nuevoValor){
        this.$emit('emitDialog', nuevoValor)
        this.$store.commit("MOD_pagos/cleanRecurso")
        this.$store.commit("MOD_pagos/cleanComentarios")
        this.$store.commit("MOD_pagos/clearArchivos")
      }
    },
    cargando() { return this.$store.getters["MOD_pagos/getCargando"]; }
  },
  methods: {
    async subirArchivo() {
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/subirArchivo", {
        'nombre': this.nombre_archivo, 'archivo': this.PDFBase64, 'recurso': this.recurso.id
      });
      this.$store.commit("alerta/addAlertas", { 'estado': estado, 'mensaje': mensaje, 'error': error, });
    },
    async eliminarArchivoRecurso({confirmacion, id, indice }){
      if(confirmacion){
        const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/eliminarArhivoRecurso", { 'id': id, 'indice': indice })
        this.$store.commit("alerta/addAlertas", { 'estado': estado, 'mensaje': mensaje, 'error': error, });
      }
    }
  },
};
</script>