<template>
  <v-toolbar dense elevation="1">
    <v-toolbar-title v-text="'Clientes'" />
    <v-spacer />
      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on, attrs }">
          <v-btn small color="success" v-bind="attrs" v-on="on">
            <v-icon left>mdi-bank</v-icon>Crear
          </v-btn>
        </template>
        <v-card class="mx-auto" max-width="500">
          <v-toolbar elevation="1" dense>
            <v-toolbar-title>Crear nuevo cliente</v-toolbar-title>
            <v-spacer />
            <v-btn icon color="error" @click="cerrarDialogo()">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-card-text>
            <v-form ref="formulario" v-model="validacion" lazy-validation>
              <v-text-field
                v-model="nombre"
                label="Nombre o razón social"
                :rules="[(v) => !!v || 'El nombre o razón social es necesario']"
              />

              <v-text-field
                v-model="rfc"
                label="RFC"
                :rules="[(v) => !!v || 'El RFC es necesario']"
              />

              <v-textarea
                v-model="direccion"
                label="Dirección"
                :rules="[(v) => !!v || 'La Dirección es necesaria']"
              />
            </v-form>
            <v-card-actions>
              <v-spacer />
              <v-btn class="error" @click="cerrarDialogo()">Cancelar</v-btn>
              <v-btn class="success" @click="crear()" :disabled="!validacion">Crear</v-btn>
            </v-card-actions>
          </v-card-text>
        </v-card>
      </v-dialog>
  </v-toolbar>
</template>

<script>
export default {
  data: () => ({
    validacion: false,
    nombre: null,
    rfc: null,
    direccion: null,
    dialog: false,
  }),
  methods: {
    async crear() {
      if (this.$refs.formulario.validate()) {
        const { nombre, rfc, direccion } = this;
        const { error, mensaje, estado } = await this.$store.dispatch("MOD_clientes/nuevo", {
          nombre, rfc, direccion
        });
        if(!error){ this.$refs.formulario.reset() }
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, 'error': error })
      }
    },
    cerrarDialogo(){
      this.$refs.formulario.reset()
      this.dialog = false
    }
  },
};
</script>
