<template>
    <v-row>
      <v-col cols="12" sm="9" md="9" lg="9" xl="9">
        <v-stepper v-model="e1">
          <v-stepper-header>
            <v-stepper-step :complete="e1 > 1" step="1">
              Paso 1 <br />
              Inmuebles
            </v-stepper-step>

            <v-divider />

            <v-stepper-step :complete="e1 > 2" step="2">
              Paso 2 <br />
              Trabajos
            </v-stepper-step>

            <v-divider />

            <v-stepper-step step="3">
              Paso 3 <br />
              Asignar
            </v-stepper-step>
          </v-stepper-header>

          <v-stepper-items>
            <v-stepper-content step="1">
              <CompPru10 />
              <br />
              <v-btn
                color="success"
                block
                @click="e1 = 2"
                :disabled="isInmueblesSeleccionados"
              >
                Continue
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="2">
              <v-btn color="warning" block @click="e1 = 1">
                Regresar
              </v-btn>
              <br />
              <CompPru11 />
              <br />
              <v-btn
                color="primary"
                @click="e1 = 3"
                block
                :disabled="asignaciones_v.length == 0 ? true : false"
              >
                Continue
              </v-btn>
            </v-stepper-content>
            <v-stepper-content step="3">
              <v-btn color="warning" block @click="e1 = 2">
                Regresar
              </v-btn>
              <br />
              <CompPru12 />
              <br />

              <v-btn color="primary" block @click="(e1 = 1), asignar()">
                Finalizar
              </v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-col>
      <v-col cols="12" sm="3" md="3" lg="3" xl="3">
        <v-card class="mx-auto" outlined>
          <v-card-title>Resumen </v-card-title>
          <div class="text-end">
            <v-btn rounded color="error" @click="$router.go(-1)">
              Cancelar
              <v-icon right>mdi-close-circle-outline</v-icon>
            </v-btn>
          </div>
        </v-card>
      </v-col>
    </v-row>
</template>

<script>
import CompPru10 from "../../components/TablaBuscarTrabajosInmueble.vue";
import CompPru11 from "../../components/FormularioTrabajoInmuebles.vue";
import CompPru12 from "../../components/TablaTrabajoInmuebles.vue";

export default {
  name: "Eliminar",
  components: {
    CompPru10,
    CompPru11,
    CompPru12,
  },
  data: () => ({
    e1: 1,
    inmuebles: [],
  }),
  computed: {
    asignaciones() { return this.$store.getters["MOD_trabajos/getAsignaciones"]; },
    asignaciones_v() { return this.$store.getters["MOD_trabajos/getAsignacionesVerificadas"] },
    isInmueblesSeleccionados() {
      return this.$store.getters["MOD_inmuebles/getInmueblesSeleccionados"].length ? false : true;
    },
  },
  methods: {
    async asignar() {
      const { asignaciones_v } = this;
      const {error, estado, mensaje} = await this.$store.dispatch("MOD_trabajos/confirmarAsignacion", asignaciones_v);
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    },
  },
};
</script>
